:root {
  --PrimaryColor: hsl(199,100%,33%);
  --SecondaryColor: hsl(187,85%,43%);
  --gradientColor: linear-gradient(to right, hsl(187,85%,43%), hsl(199,100%,33%));
  --whiteColor: hsl(0,0%,100%);
  --blackColor: hsl(201,33%,16%);
  --textColor: hsl(240,4%,36%);
  --whiteColorDeam: hsl(0,0%,93%);
  --greyText: rgb(190,190,190);
  --inputColor: rgb(239,239,239);
  --bodyColor: rgb(240,240,240);
  --cardBG: rgb(225,225,235);
}

.header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--whiteColorDeam);
  width: 100%;
  padding: 1rem;
  z-index: 1000;
  box-shadow: 0 2px 4px rgba(2, 15, 29, 0.904);
}

.logo {
  color: var(--blackColor);
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon {
  font-size: 25px;
  color: var(--PrimaryColor);
  transform: translateY(5px);
}

.logo h1 {
  display: flex;
  align-items: center;
  font-size: 1.5rem; /* Adjust size if needed */
}

.logo h1 .icon {
  margin-right: 8px; /* Space between the icon and the text */
}

.logo h1 .flag-emoji {
  font-size: 1.5rem; /* Adjust size if needed */
  margin-left: 8px; /* Space between the text and the flag */
}

.navBar {
  position: absolute;
  background: var(--whiteColorDeam);
  height: -moz-max-content;
  height: max-content;
  width: 80%;
  border-radius: 1rem;
  top: -500rem; /* Initially hidden off-screen */
  left: 50%;
  padding: 1rem;
  transform: translate(-50%);
  z-index: 2000;
  box-shadow: 0 2px 4px rgba(2, 15, 29, 0.904);
  transition: top 0.75s ease-in-out; /* Increase duration to slow down the transition */
}

.activeNavbar {
  top: 7rem; /* Adjust this value as needed */
}

.navLists {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding: 0.5rem 0;
  margin: auto;
}

.navItem {
  padding: 0.5rem 0;
}

.navLink {
  color: var(--textColor);
  font-size: 0.9rem;
  font-weight: 600;
  transition: color 0.3s;
}

.navLink:hover {
  color: var(--PrimaryColor);
}

.btn {
  margin-top: 1rem;
  color: var(--whiteColor);
}

.closeNavbar {
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  color: var(--PrimaryColor);
}

.closeNavbar:hover {
  color: var(--SecondaryColor);
}

.toggleNavbar .icon {
  font-size: 25px;
  color: var(--PrimaryColor);
}

.toggleNavbar .icon:hover {
  color: var(--SecondaryColor);
}

@media screen and (min-width: 769px) {
  .toggleNavbar,
  .closeNavbar {
    display: none;
  }
  .header {
    padding: 1.5rem 2rem;
  }
  .header .navBar {
    position: static;
    width: auto;
    height: auto;
    border-radius: 0;
    top: auto;
    left: auto;
    padding: 0;
    transform: none;
    box-shadow: none;
    transition: none;
  }
  .header .navBar .navLists {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }
  .header .navBar .navLists .navItem {
    padding: 0;
  }
  .header .navBar .navLists .navItem .navLink {
    color: var(--textColor);
    padding: 0 0.45rem;
    font-size: 0.9rem;
    font-weight: 500;
  }
  .header .navBar .navLists .navItem .navLink:hover {
    color: var(--PrimaryColor);
  }
  .header .navBar .navLists .btn {
    margin-left: 1rem;
  }
  .header .navBar .navLists .btn a {
    color: var(--whiteColor);
    font-weight: 500;
  }
}
@media screen and (min-width: 960px) {
  .header .navBar .navLists .navItem {
    padding: 0 0.7rem;
  }
}
@media screen and (min-width: 1240px) {
  .header {
    padding: 1rem 8.5rem;
  }
}/*# sourceMappingURL=navbar.css.map */